.container {
    border: solid 5px #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin: 40% 3%;
}

.flag {
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}

.description {
    width: 200px;
    height: 200px;
    position: absolute;
    display: flex;
    transform: scale(0);
    transition: .5s;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.411);
    backdrop-filter: blur(5px);
    color: white;
}

.container:hover .description {
    transform: scale(1);
}

.description>h2 {
    width: 180px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.description>h2:hover {
    width: 150px;
    white-space: pre-wrap;
    overflow: auto;
}

.description>h2::-webkit-scrollbar{
    width: 0;
}